import React from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import TabButtonComponent from './TabButtonComponent';
import TabContentComponent from './TabContentComponent';


// tircky way skip `eslint: no-unused-vars`
// import remove `Router`, will missing Route
const tircky = () => <Router></Router>;

const TabComponent = (props) => {
  tircky();
  const { city } = props;

  return (
    <React.Fragment>
      <Link to={`/${city.id}`}>
        <TabButtonComponent city={city} onClick={props.onClick} />
      </Link>

      <Route
        path={`/${city.id}`}
        render={() => <TabContentComponent city={city} />}
      />
    </React.Fragment>
  );
};

export default TabComponent;
