import React, { Component } from 'react';
import { Redirect } from 'react-router';
import { BrowserRouter as Router } from 'react-router-dom';
import FooterComponent from './components/FooterComponent';
import TabComponent from './components/TabComponent';
import { data as Data, updated_at as updatedAt } from './data/data.json';
import IdGenerator from './lib/Utils';


const currentPath = () => window.location.pathname.replace('/', '');

const currentPathHasData = (importData) => {
  const path = currentPath();
  if (importData[path]) {
    return true;
  }
  return false;
};

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: Data,
      current: 'taipei-city',
    };

    this.updatedAt = new Date(updatedAt).toString();
  }

  componentWillMount() {
    if (currentPathHasData(Data)) {
      const path = currentPath();
      this.handleClick(path);
    }
  }

  handleClick(cityId) {
    this.setState({
      current: cityId,
    });
  }

  renderTabs() {
    const citys = Object.keys(this.state.data);
    const tabs = [];
    citys.forEach((city, key) => {
      const cityProps = this.state.data[city];
      if (!cityProps) return;

      cityProps.id = IdGenerator(cityProps.en_county_name);
      cityProps.checked = cityProps.id === this.state.current;

      if (!currentPathHasData(Data)) {
        tabs.push(<Redirect to={`/${this.state.current}`} key={`no-match-${key}`} />);
      }

      tabs.push(
        <TabComponent
          key={`tab-${city}`}
          city={cityProps}
          onClick={cityName => this.handleClick(cityName)}
        />,
      );
    });

    return (
      <div className="tabs">
        {tabs}
      </div>
    );
  }

  render() {
    return (
      <Router>
        <div className="App">
          <h1>weather-crawler</h1>

          {this.renderTabs()}

          <span className="mini right">
            {this.updatedAt}
          </span>

          <FooterComponent />
        </div>
      </Router>
    );
  }
}

export default App;
